import { css } from 'styled-components';

export const mediaObject = css`
	display: flex;
	flex-direction: row;
	align-items: center;
`;

export const mediaObjectText = css`
	flex: 1 1 auto;
`;

export const mediaObjectFigure = css`
	max-width: none;
	flex: 0 0 auto;
`;



